.button-inline {
  border-width: 0;
  background: transparent;
  color: inherit;
  text-align: inherit;
  -webkit-font-smoothing: inherit;
  padding: 0;
  font-size: inherit;
  cursor: pointer;
  outline: none;
  text-shadow: 3px 3px 3px #ccc !important;
  -webkit-transition: 0.3s ease-in;
  -o-transition: 0.3s ease-in;
  transition: 0.3s ease-in;
}

.button-inline:hover{
  font-size: 18px;
}
  
.button-active {
  border-radius: 0;
  border-bottom: 1px solid #38BB6C;
}


